.review-card-wrapper .content .description {
    height: 80px;
}
.review-card-wrapper .content .description p {
    overflow: hidden;
    text-overflow: ellipsis;
    width: 24vw;
    display: -webkit-box; 
    -webkit-box-orient: vertical; 
    -webkit-line-clamp: 3; 
}

@media screen and (max-width: 1080px) {
    .review-card-wrapper .content .description p {
        width: 100%;
    }
}